#scrollUp {
	@include fs(0);
	width: 40px;
	height: 40px;
	line-height: 40px;
	text-align: center;
	background: darken($white,40%);
	color: $white;
	right: 20px;
	bottom: 20px;
	@include media-breakpoint-down(xs) {
		display: none !important; }
	&:before {
		content: '\f106';
		@include fs(18rem); }
	&:hover {
		background: darken($white,60%); } }
