.sidebar-nav {
	background: darken($white,9%);
	margin-bottom: 10px;
	&.user-nav {
		ul {
			li {
				a {
					height: 40px;
					.icon {
						width: 30px;
						.material-icons {
							@include fs(18rem); } }
					.text {
						@include fs(13rem); } } } } }
	ul {
		li {
			a {
				@include flexbox;
				height: 50px;
				align-items: center;
				align-content: center;
				padding: 0px 15px;
				color: darken($white,60%);
				.icon {
					width: 34px;
					.material-icons {
						vertical-align: middle; } }
				.text {
					@include fs(15rem); }
				// strong
				/// color: $red
 }				/// font-weight: 600
			&.active {
				a {
					color: $main;
					font-weight: 700;
					background: darken($white,16%);
					.text {
						font-weight: 700; }
					strong {
						font-weight: 700;
						color: $red; } } } } } }
.requirement-page {
	margin-bottom: 60px;
	background: $white;
	border: 1px solid darken($white,10%);
	border-top: none;
	.worker-detail {
		.worker-project {
			.nav-tabs {
				.nav-item {
					&:last-child {
 } } }						// display: block
			.tab-content {
				.tab-pane {
					background: $white;
 } } } }					// display: block
	.btn-wrap {
		@include flexbox;
		.btn-workerlist {
			@include fs(18rem);
			color: $blue;
			span {
				font-weight: 600; }
			&:before {
				content: '\f104';
				margin-right: 5px; }
			&:hover {
				color: $dblue;
				span {
					text-decoration: underline; } } }
		.btn-selectworker {
			display: block;
			@include fs(18rem);
			font-weight: 700;
			color: $main;
			background: $yellow;
			text-transform: uppercase;
			padding: 15px 30px;
			&:hover {
				background: $amber; } } }
	.select-des {
		margin-top: 10px;
		@include fs(14rem);
		font-style: italic;
		text-align: right;
		color: darken($white,40%); } }
.requirement-list {
	.requirement-group {
		@include pos(rel,null);
		@include flexbox;
		flex-wrap: wrap;
		padding: 40px;
		@include media-breakpoint-down(xs) {
			padding: 15px; }
		&:after {
			content: '';
			display: block;
			@include pos(abs, null 40px 0 40px);
			height: 1px;
			background: darken($white,10%); }
		&:last-child {
			&:after {
				display: none; } }
		.requirement-info {
			width: calc(100% - 140px);
			line-height: 1.8;
			@include media-breakpoint-down(xs) {
				width: 100%; }
			.requirement-name {
				@include fs(18rem);
				font-weight: 700;
				color: $main;
				margin-bottom: 5px;
				@include media-breakpoint-down(xs) {
					@include fs(15rem); } } }
		.btn-wrap {
			width: 140px;
			@include flexbox;
			flex-wrap: wrap;
			align-items: center;
			align-content: center;
			@include media-breakpoint-down(xs) {
				width: 100%; }
			a {
				@include flexbox;
				flex-wrap: wrap;
				justify-content: center;
				align-items: center;
				align-content: center;
				text-align: center;
				@include fs(16rem);
				text-transform: uppercase;
				width: 100%;
				height: 50px;
				background: $yellow;
				@include media-breakpoint-down(xs) {
					height: 40px;
					@include fs(14rem);
					display: inline-block;
					vertical-align: middle;
					line-height: 40px;
					width: auto;
					padding: 0px 20px; }
				+ a {
					margin-top: 10px;
					@include media-breakpoint-down(xs) {
						margin-top: 0;
						margin-left: 10px; } }
				&:hover {
					background: $amber; }
				&.active {
					background: $blue;
					color: $white; }
				&.btn-cancel {
					background: darken($white,5%);
					&:hover {
						background: darken($white,20%); } }
				&.btn-repost {
					background: $yellow;
					&:hover {
						background: $amber; } }
				.material-icons {
					// +fs(42rem)
					display: none;
					text-align: center;
					// width: 100%
 } } }					// padding: 5px 0
		.owner-new-project-content {
			width: 100%;
			display: none;
			margin-top: 20px;
			@include pos(rel,null);
			&.active {
				display: block; }
			.close-collapse-content {
				width: 30px;
				height: 30px;
				line-height: 30px;
				@include bor-rad(50px);
				background: $red;
				color: $white;
				text-shadow: none;
				@include fs(18rem);
				text-align: center;
				@include pos(abs, 10px 10px null null); }
			.post-page {
				margin: 0;
				padding: 20px;
				border: 1px solid darken($white,10%);
				.page-title {
					@include fs(16rem);
					margin-bottom: 20px; }
				.info-table {
					margin-bottom: 0;
					td {
						@include media-breakpoint-down(xs) {
							display: block;
							width: 100%;
							padding: 10px 0px 0;
							&:first-child {
								width: 100%;
								border-bottom: none; }
							&:last-child {
								padding: 10px 0;
								text-align: left;
								@include fs(15rem);
								* {
									font-weight: 700; } } } } } } } }
	.btn-loadmore {
		@include flexbox;
		justify-content: center;
		padding: 30px 0;
		a {
			display: block;
			@include bor-rad(0);
			@include fs(18rem);
			font-weight: 700;
			border: none;
			box-shadow: none;
			outline: none;
			color: $main;
			background: $yellow;
			text-transform: uppercase;
			padding: 15px 40px;
			line-height: 1;
			&:active, &:focus {
				outline: none;
				border: none; }
			&:hover {
				background: $amber; } } } }
