						width: calc(100% - 30px);
						&:hover {
							color: $blue; } }
					.material-icons {
						color: $blue;
						width: 30px;
						text-align: right;
						@include fs(28rem); } } }
			.checkbox {
				margin-bottom: 20px; }
			.btn-wrap {
				margin-bottom: 10px;
				@include flexbox;
				justify-content: space-between;
				@include media-breakpoint-down(xs) {
					flex-wrap: wrap; }
				a {
					display: block;
					line-height: 60px;
					padding: 0px 25px;
