.sidebar-user {
	.user {
		background: darken($white,60%);
		figure {
			text-align: center;
			padding: 20px;
			.user-img {
				width: 90px;
				height: 90px;
				margin: 0px auto 10px;
				@include bor-rad(50%);
				overflow: hidden;
				@include pos(rel,null);
				&:before {
					content: '\f030';
					@include pos(abs, 0 null 0 0);
					right: 0;
					background: $white;
					color: $main;
					@include fs(32rem);
					@include flexbox;
					justify-content: center;
					align-items: center;
					opacity: 0;
					visibility: hidden;
					@include transition(all,0.5s); }
				&:hover {
					&:before {
						visibility: visible;
						opacity: 0.3; } }
				img {
					width: 100%;
					height: 100%;
					@include bor-rad(50%); } }
			.fileinput-button {
				display: none; }
			figcaption {
				.user-name {
					@include fs(15rem);
					color: $white;
					font-weight: 600;
					margin-bottom: 5px; }
				.user-info {
					ul {
						li {
							@include fs(13rem);
							color: $white;
							line-height: 1.3; } } } } } } }

.user-page {
	margin-bottom: 60px;
	background: $white;
	padding: 40px;
	border: 1px solid darken($white,10%);
	border-top: none;
	@include media-breakpoint-down(xs) {
		padding: 20px; }
	.account-management {}
	.profile-group {
		margin-bottom: 40px;
		&:last-child {
			margin-bottom: 0; } }
	.form-wrap {
		input[type="submit"], .btn-primary {
			display: block;
			@include bor-rad(0);
			@include fs(18rem);
			font-weight: 700;
			border: none;
			box-shadow: none;
			outline: none;
			color: $main;
			background: $yellow;
			text-transform: uppercase;
			padding: 15px 40px;
			line-height: 1;
			@include media-breakpoint-down(xs) {
				padding: 8px 20px;
				@include fs(14rem); }
			&:active, &:focus {
				outline: none;
				border: none; }
			&:hover {
				background: $amber; } } } }
.favorite-worker {
	.favorite-list {
		margin: 0px -40px -40px;
		@include media-breakpoint-down(xs) {
			margin: 0px -20px -20px; } }
	.worker {
		&:nth-child(even) {
			background: darken($white,1%); }
		> figure {
			padding: 20px 40px;
			@include flexbox;
			align-items: center;
			align-content: center;
			@include media-breakpoint-down(xs) {
				padding: 15px;
				align-items: flex-start;
				align-content: flex-start; }
			.worker-img {
				width: 120px;
				height: 120px;
				@include bor-rad(50%);
				overflow: hidden;
				@include media-breakpoint-down(xs) {
					width: 70px;
					height: 70px; }
				img {
					width: 100%;
					height: 100%;
					@include bor-rad(50px); } }
			figcaption {
				width: calc(100% - 120px);
				padding-left: 30px;
				@include pos(rel,null);
				@include media-breakpoint-down(xs) {
					padding-left: 10px;
					width: calc(100% - 70px); }
				.worker-name {
					@include fs(24rem);
					margin-bottom: 10px;
					@include media-breakpoint-down(xs) {
						@include fs(14rem);
						font-weight: 600;
						text-transform: uppercase; }
					a {
						color: $main;
						&:hover {
							color: $blue; } }
					.add-favorite-button {
						&:hover {
							color: lighten($red,20%); }
						&.active {
							color: lighten($red,10%); } } }
				.worker-des {
					ul {
						margin-bottom: 10px;
						li {
							@include fs(15rem);
							@include pos(rel,null);
							padding-left: 20px;
							margin-bottom: 5px;
							@include media-breakpoint-down(xs) {
								@include fs(13rem); }
							&:last-child {
								margin-bottom: 0; }
							.fa {
								@include pos(abs, 2px null null 0); } } }
					.btn-viewinfo {
						display: inline-block;
						@include fs(14rem);
						line-height: 34px;
						padding: 0px 20px;
						background: $yellow;
						color: $main;
						@include media-breakpoint-down(xs) {
							line-height: 30px;
							@include fs(13rem); } } }
				.rating {
					display: inline-block;
					margin-left: 10px;
					@include pos(abs, 0 0 null null);
					@include media-breakpoint-down(xs) {
						@include pos(static,unset); }
					.fa {
						display: inline-block;
						width: auto;
						color: darken($white,30%);
						@include media-breakpoint-down(xs) {
							@include fs(13rem); }
						&.active {
							color: $yellow; } } } } } } }
.pro-detail-content {
	display: none;
	width: 100%;
	// background: $footer
	@include pos(rel,null);
	padding: 0px 0px 30px;
	margin-top: 15px;
	@include media-breakpoint-down(md) {
		padding: 0px 0px 20px; }
	&:before {
		content: '';
		display: block;
		width: 0;
		height: 0;
		border: 10px solid transparent;
		border-top: none;
		border-bottom-color: $footer;
		@include pos(abs, -10px 55px null null);
		@include transform(translateX(50%));
		display: none;
		@include media-breakpoint-down(xs) {
			right: unset;
			left: 35px;
			display: none; } }
	&.active {
		display: block; }
	.worker-detail {
		padding: 10px;
		@include pos(rel,null);
		@include media-breakpoint-down(md) {
			padding: 0; }
		.worker-info {
			display: none;
			figure {
				.worker-img {
					margin: 0; } } }
		.worker-project {
			.tab-content {
				margin-bottom: 0; } } } }
.report-page {
	.report-form {}
	.contact-info {
		@include fs(15rem);
		ul {
			li {
				border-bottom: 1px dashed darken($white,10%);
				padding: 5px 0;
				a {
					color: $blue;
					// padding: 5px 0
					&.hotline {
						font-weight: 600;
						color: $main; }
					&:hover {
						color: $yellow; } } } } }
	.form-wrap {
		.form-group {
			.form-control {
				border: 1px solid darken($white,10%);
				font-weight: normal;
				min-height: 190px; } } } }

.faq-list {
	margin-bottom: 60px;
	.faq {
		margin-bottom: 15px;
		@include bor-rad(5px);
		overflow: hidden;
		.faq-title {
			@include fs(18rem);
			font-weight: 600;
			padding: 10px 20px;
			background: #a4d0eb;
			cursor: pointer;
			@include transition(all,0.3s);
			@include media-breakpoint-down(sm) {
				@include fs(15rem);
				padding: 10px 15px; }
			&:hover {
				color: $blue; } }
		.faq-content {
			display: none;
			background: #daf0fc;
			padding: 20px;
			@include media-breakpoint-down(sm) {
				padding: 15px; } }
		&.active {
			.faq-title {}
			.faq-content {} } } }
