.banner {
	@include pos(rel,null);
	.desc {
		@include pos(abs, null null 10px 50%);
		@include transform(translateX(-50%)); }
	.btn-homebutton {
		z-index: 3;
		@include fs(13rem);
		white-space: nowrap; }
	.slick-prev, .slick-next {
		@include pos(abs, 50% null null null);
		@include transform(translate(0,-50%));
		@include fs(0);
		z-index: 5;
		opacity: 0;
		color: $white;
		text-shadow: 0 3px 10px color-rgba($black,0.5);
		@include transition(all,0.5s);
		&:before {
			content: '';
			@include fs(32rem); } }
	.slick-prev {
		left: 30px;
		&:before {
			content: '\f104'; } }
	.slick-next {
		right: 30px;
		&:before {
			content: '\f105'; } }
	&:hover {
		.slick-prev, .slick-next {
			opacity: 0.5;
			&:hover {
				opacity: 1; } } }
	.item {
		.img {
			display: block; } } }
