// // Set Link Hover
@mixin link ($link, $hover:$link, $active:$link, $visit:$link) {
	color: #{$link};
	&:visited {
		color: #{$visit}; }
	&:hover {
		color: #{$hover}; }
	&:active, &:focus , &.active, &.activated {
		color: #{$active}; } }

// Set bg hover
@mixin bg ($link, $hover:$link, $active:$link) {
	@if $link != "none" {
		background-color: #{$link};
		&:hover {
			background-color: #{$hover}; }
		&:active, &:focus , &.active, &.activated {
			background-color: #{$active}; } }
	@else {
		background: none;
		&:hover {
			background-color: #{$hover}; }
		&:active, &:focus , &.active, &.activated {
			background-color: #{$active}; } } }

// Flexbox
@mixin flexbox {
	display: -webkit-box;
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flexbox;
	display: flex; }

// Font size px to rem
@mixin fs($size) {
	font-size: ($size / 16); }

@mixin mainbutton() {
	display: inline-block;
	line-height: 40px;
	@include fs(18rem);
	font-family: 'Muli';
	text-transform: uppercase;
	font-weight: 600;
	color: $main;
	background: $yellow;
	padding: 0px 50px;
	border: none;
	@include transition(all,0.5s);
	@include media-breakpoint-down(xs);
	&:hover {
		background: lighten($yellow,20%); } }
