.block-gyprocLinks {
	padding: 60px 0;
	.col-12 {
		margin-bottom: 30px; }
	figure {
		@include flexbox;
		flex-wrap: wrap;
		justify-content: center;
		.icon {
			margin-bottom: 15px;
			a {
				@include flexbox;
				justify-content: center;
				align-items: center;
				width: 120px;
				height: 120px;
				@include bor-rad(100px);
				background: $yellow;
				@include transition(all,0.5s);
				img {
					@include transition(all,0.5s); } } }
		figcaption {
			width: 100%;
			text-align: center;
			h2 {
				a {
					color: $main;
					@include fs(16rem);
					font-weight: 700; } } }
		&:hover {
			.icon {
				a {
					background: $blue;
					img {
						filter: invert(100%); } } } } } }
